* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: white;
}

body {
  background-color: #1d1d1d;
}

.App {
  text-align: center;
  margin-top: 40px;
}

.history {
  display: none;
}

.history .history-heading {
  border: 1px solid #fff;
  margin: 10px 20px;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 3px rgba(255, 255, 255, 0.4);
          box-shadow: 0 0 3px rgba(255, 255, 255, 0.4);
}

.history .history-heading > h1 {
  cursor: pointer;
}

.history .history-heading .list {
  height: 0;
  overflow: hidden;
  -webkit-transition: height 0.3s ease;
  transition: height 0.3s ease;
}

.history .history-heading .list .list-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding: 0 20px;
  border-radius: 3px;
  font-size: 25px;
  margin: 5px 10px;
  -webkit-transition: -webkit-transform 0.1s ease;
  transition: -webkit-transform 0.1s ease;
  transition: transform 0.1s ease;
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}

.history .history-heading .list .list-item span {
  cursor: pointer;
  font-weight: bold;
}

.history .history-heading .list .list-item span:hover {
  color: gray;
}

.history .history-heading .list .list-item:hover {
  -webkit-box-shadow: 0 0 3px #404040;
          box-shadow: 0 0 3px #404040;
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.history .history-heading .list .no-history {
  color: #858585;
}

header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border: 1px solid #fff;
  margin: 10px;
}

header > * {
  padding: 0 20px;
  margin: 0 10px;
  cursor: pointer;
}

.schedule-heading {
  border: 1px solid #fff;
  margin: 10px 20px;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 3px rgba(255, 255, 255, 0.4);
          box-shadow: 0 0 3px rgba(255, 255, 255, 0.4);
}

.schedule-heading > h1 {
  cursor: pointer;
}

.schedule-heading .list {
  height: 0;
  overflow: hidden;
  -webkit-transition: height 0.3s ease;
  transition: height 0.3s ease;
}

.schedule-heading .list .list-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding: 0 20px;
  border-radius: 3px;
  font-size: 25px;
  margin: 5px 10px;
  -webkit-transition: -webkit-transform 0.1s ease;
  transition: -webkit-transform 0.1s ease;
  transition: transform 0.1s ease;
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}

.schedule-heading .list .list-item:hover {
  -webkit-box-shadow: 0 0 3px #404040;
          box-shadow: 0 0 3px #404040;
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.schedule-heading .list .no-history {
  color: #858585;
}

.schedule-heading form {
  height: 0px;
  overflow: hidden;
  -webkit-transition: height 0.3s ease-out;
  transition: height 0.3s ease-out;
  margin-top: 30px;
}

.schedule-heading form.active {
  height: 150px;
}

.schedule-heading form input,
.schedule-heading form select {
  margin: 10px;
  background-color: black;
  border: 0;
  outline: 0;
  height: 1.5rem;
}

.schedule-heading form label {
  margin: 10px;
  font-size: 20px;
}

.schedule-heading form button {
  display: block;
  margin: 20px auto;
  font-size: 20px;
  padding: 5px 10px;
  border: transparent;
  background-color: #4661c4;
  border-radius: 10px;
  cursor: pointer;
}
