* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: white;
}

body {
  background-color: #1d1d1d;
}

.App {
  text-align: center;
  margin-top: 40px;
}

.history {
  display: none;

  .history-heading {
    border: 1px solid #fff;
    margin: 10px 20px;
    border-radius: 10px;
    box-shadow: 0 0 3px rgba($color: #fff, $alpha: 0.4);

    > h1 {
      cursor: pointer;
    }

    .list {
      height: 0;
      overflow: hidden;
      transition: height 0.3s ease;

      .list-item {
        display: flex;
        justify-content: space-between;
        border-top: 1px solid rgba($color: #fff, $alpha: 0.5);
        border-bottom: 1px solid rgba($color: #fff, $alpha: 0.5);
        padding: 0 20px;
        border-radius: 3px;
        font-size: 25px;
        margin: 5px 10px;
        transition: transform 0.1s ease;

        span {
          cursor: pointer;
          font-weight: bold;

          &:hover {
            color: rgba($color: grey, $alpha: 1);
          }
        }

        &:hover {
          box-shadow: 0 0 3px rgba($color: #404040, $alpha: 1);
          transform: scale(1.01);
        }
      }

      .no-history {
        color: #858585;
      }
    }
  }
}

header {
  display: flex;
  justify-content: center;
  border: 1px solid #fff;
  margin: 10px;

  > * {
    padding: 0 20px;
    margin: 0 10px;
    cursor: pointer;
  }
}

.schedule-heading {
  border: 1px solid #fff;
  margin: 10px 20px;
  border-radius: 10px;
  box-shadow: 0 0 3px rgba($color: #fff, $alpha: 0.4);

  > h1 {
    cursor: pointer;
  }

  .list {
    height: 0;
    overflow: hidden;
    transition: height 0.3s ease;

    .list-item {
      display: flex;
      justify-content: space-between;
      border-top: 1px solid rgba($color: #fff, $alpha: 0.5);
      border-bottom: 1px solid rgba($color: #fff, $alpha: 0.5);
      padding: 0 20px;
      border-radius: 3px;
      font-size: 25px;
      margin: 5px 10px;
      transition: transform 0.1s ease;

      &:hover {
        box-shadow: 0 0 3px rgba($color: #404040, $alpha: 1);
        transform: scale(1.01);
      }
    }

    .no-history {
      color: #858585;
    }
  }

  form {
    height: 0px;
    overflow: hidden;
    transition: height 0.3s ease-out;
    margin-top: 30px;

    &.active {
      height: 150px;
    }

    input,
    select {
      margin: 10px;
      background-color: black;
      border: 0;
      outline: 0;
      height: 1.5rem;
    }

    label {
      margin: 10px;
      font-size: 20px;
    }

    button {
      display: block;
      margin: 20px auto;
      font-size: 20px;
      padding: 5px 10px;
      border: transparent;
      background-color: #4661c4;
      border-radius: 10px;
      cursor: pointer;
    }
  }
}
